@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap);
.App {
  text-align: center;

  overflow-x: hidden;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

.homeContainer {
  -webkit-animation: fadeIn;
          animation: fadeIn;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.errors-container {
  margin-top: 20px;
}

.error-box {
  display: none;
}

.error-box.active {
  display: flex;
  opacity: 1;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;

  margin: 10px 0px 10px 0px;
  background-color: #fff;
  padding: 0px 15px;
  border-radius: 5px;
  font-weight: bold;

  background-color: #ffdb39;
  color: #272727;
  font-size: 1.2em;
  cursor: default;
}

.error-box.credentials {
  background-color: #ff3939;
  color: #fff;
}

.error-box p {
  width: 95%;
  text-align: start;
}

.close {
  width: 5%;
  height: auto;
  opacity: 0.6;

  cursor: pointer;
}

.errors-container .active {
  display: flex;
  /*animation: fade linear 0.1s;*/
  -webkit-animation: fadeInTopRight;
          animation: fadeInTopRight;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
}

.loading-message {
  width: 100vw;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.info-message {
  color: #fff;
}

#link-loading-animation {
  width: 250px;
  height: 250px;
}

#loading-circle-animation {
  width: 150px;
  height: 150px;
}

.icon-container {
  margin: 10px;
}

.navigation-profile-img {
  width: 75px;
  height: 75px;
  object-fit: cover;
  border: 2px solid rgba(0, 0, 0, 0.25);
  border-radius: 100%;

  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.sign-btn {
  color: #fff;
  border-radius: 5px;
  transition: all linear 0.2s;
}

.enable:hover {
  color: #006eff;
  background-color: #fff;
}

.disable {
  cursor: default;
  opacity: 0.5;
}

.Tilt {
  background: linear-gradient(
    89deg,
    rgba(85, 61, 141, 0.75) 0%,
    rgba(0, 110, 255, 0.75) 100%
  );

  display: flex;
  align-items: center;
  justify-content: center;
  width: 10vw;
}

.Tilt img {
  width: 100%;
}

@media screen and (max-width: 1000px) {
  .Tilt {
    width: 13vw;
  }
}

@media screen and (max-width: 650px) {
  .Tilt {
    width: 16vw;
  }
}

@media screen and (max-width: 450px) {
  .Tilt {
    width: 19vw;
  }
}

.image-link-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 10px;
}

.link-form {
  width: 65vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;

  padding: 15px;
  border: none;
  border-radius: 5px;

  background-color: rgba(0, 0, 0, 0.25);
  background-image: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 35px,
    rgba(255, 255, 255, 0.25) 35px,
    rgba(255, 255, 255, 0.25) 70px
  );

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.input-title {
  color: #fff;
  font-weight: bold;
  cursor: default;
}

.input-field,
.input-btn {
  margin: 0 5px;
  outline: none;
  color: #215bd3;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.65);

  border: 2px solid #006eff00;
  border-radius: 5px;
  text-align: center;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);

  transition: all ease 0.25s;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.input-field::-webkit-input-placeholder {
  color: #215bd3;
}

.input-field:-ms-input-placeholder {
  color: #215bd3;
}

.input-field::placeholder {
  color: #215bd3;
}

.input-btn:hover,
.input-field:hover,
.input-field:focus {
  border-color: #215bd3;
}

@media screen and (max-width: 850px) {
  .link-form {
    width: 75vw;
  }
}

@media screen and (max-width: 500px) {
  .link-form {
    width: 85vw;
  }
}

.image {
  border-radius: 5px;
  /* border: 3px solid #4f14f2; */
  width: 100%;
  max-width: 80vw;
  max-height: 80vh;
}

.bounding-box {
  position: absolute;
  border: 3px dashed #4f14f2;
  border-radius: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;

  transition: all linear 0.1s;
}

.bounding-box:hover {
  background-color: rgba(97, 39, 255, 0.5);
  border-style: solid;
}

.signin-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 100%;

  -webkit-animation: slideInRight;

          animation: slideInRight;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

.signin-box {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;

  border: none;
  border-radius: 10px;

  background-color: rgba(0, 0, 0, 0.1);
  box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);

  width: 700px;
}

.signin-box h1 {
  color: #fff;
  font-weight: bold;
  font-size: 3em;
}

.horizontal-line {
  display: none;
  margin-bottom: 20px;
}

.signin-inputs-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.signin-inputs-div input {
  background-color: transparent;
  font-size: 1.2em;
  letter-spacing: 3px;
  border: 2px solid #006eff;
  border-radius: 5px;
  padding: 10px 20px;
  margin: 10px 0px 10px 0px;
  width: 60%;
  transition: all ease-in-out 0.1s;
  color: #fff;
}

::-webkit-input-placeholder {
  color: #fff;
}

:-ms-input-placeholder {
  color: #fff;
}

::placeholder {
  color: #fff;
}

.signin-inputs-div input:focus {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.sign-in-buttons-div {
  display: flex;
  justify-content: center;
  align-items: center;

  margin: 40px 0px 20px 0px;
  width: 100%;
}

.sign-in-buttons-div button:first-of-type {
  width: 20%;
  border-width: 10px 1px 0px 0px;
  border-radius: 10px 0px 0px 10px;
}

.sign-in-buttons-div button {
  background-color: #fff;
  padding: 5px 10px;
  width: 40%;
  font-size: 1.2em;

  border: 2px solid #006eff;
  border-width: 10px 0px 0px 1px;
  border-radius: 0px 10px 10px 0px;

  transition: all linear 0.1s;
}

.sign-in-buttons-div button:hover {
  color: #fff;
  background-color: #006eff;
}

@media (max-width: 800px) {
  .signin-box {
    width: 650px;
  }

  .signin-inputs-div input {
    width: 80%;
  }

  .sign-in-buttons-div button:first-of-type {
    width: 35%;
  }

  .sign-in-buttons-div button {
    width: 45%;
  }
}

@media (max-width: 690px) {
  .signin-box {
    width: 90%;
  }

  .horizontal-line {
    display: block;
    width: 50%;
    height: 2px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.5);
  }

  .sign-in-buttons-div {
    flex-direction: column;
  }

  .sign-in-buttons-div button:first-of-type {
    width: 80%;
    border-width: 2px;
    margin-bottom: 2px;
    border-radius: 5px;
  }

  .sign-in-buttons-div button {
    width: 80%;
    border-width: 2px;
    border-radius: 5px;
    padding: 10px 15px;
  }
}

