#modal-root {
  position: relative;
  z-index: 999;
}

.profile-modal {
  background-color: rgba(130, 152, 223, 0.8);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.modal-container {
  min-width: 40%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.35);
  padding: 20px;

  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.close-modal-x {
  position: absolute;
  top: 10px;
  right: 10px;

  color: #000;
  display: flex;
  justify-content: center;
  align-items: center;

  background-color: rgba(0, 0, 0, 0.25);
  border: 2px solid rgba(0, 0, 0, 0.25);
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 1.75rem;
  cursor: pointer;

  transition: all linear 0.1s;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.close-modal-x:hover {
  color: #fff;
  background-color: rgba(255, 0, 0, 0.5);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.modal-profile-img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 100%;

  box-shadow: rgba(149, 157, 165, 0.5) 0px 8px 24px;
}

.modal-container hr {
  width: 90%;
  margin: 5px;
  height: 2px !important;
  background-color: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 5px;
}

.modal-info-main {
  min-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-bottom: 20px;
}

.user-input-container {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  border-radius: 5px;
  padding: 0px;
  margin-bottom: 10px;

  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.user-input-container span {
  height: 100%;
  font-weight: bold;
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-input-container input {
  width: 75%;
  border: none;
  padding: 10px;
  border-radius: 0 5px 5px 0;
}

.user-input-container input::-webkit-input-placeholder {
  color: #727578;
}

.user-input-container input:-ms-input-placeholder {
  color: #727578;
}

.user-input-container input::placeholder {
  color: #727578;
}

.user-image {
  background-color: #fff;
}

.user-input-container input::-webkit-file-upload-button {
  background-color: #d1d9f2;
  border: 2px solid rgba(0, 0, 0, 0);
  border-radius: 5px;
  padding: 5px 10px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

  -webkit-transition: all linear 0.1s;

  transition: all linear 0.1s;
}

.user-input-container input::-webkit-file-upload-button:hover {
  border: 2px solid rgba(0, 0, 0, 0.15);
}

.btns-container {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.btns-container button {
  font-weight: bold;
  border: none;
  border-radius: 5px;
  padding: 15px 30px;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: all linear 0.1s;
  border: 3px solid rgba(0, 0, 0, 0);

  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

#modal-save {
  background-color: rgba(0, 196, 0, 0.5);
}

#modal-save:hover {
  background-color: rgba(0, 196, 0, 0.65);
}

#modal-cancel {
  background-color: rgba(255, 34, 34, 0.5);
}

#modal-cancel:hover {
  background-color: rgba(255, 34, 34, 0.65);
}

.btns-container button:hover {
  border-color: rgba(0, 0, 0, 0.25);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.disable-particles-container {
  position: absolute;
  bottom: 10px;
  right: 10px;
}

.disable-particles-container button {
  color: #fff;
  max-width: 200px;
  border: 2px solid rgba(255, 255, 255, 0.25);
  border-radius: 2px;
  padding: 10px 20px;
  background-color: rgba(255, 255, 255, 0.25);

  transition: all ease 0.25s;
}

.disable-particles-container button:hover {
  background-color: transparent;
  box-shadow: inset 200px 0 0 0 rgba(0, 0, 0, 0.25);
}

.particles.disabled {
  display: none;
}

* {
  outline: none;
}

::-webkit-input-placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:-ms-input-placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
input,
button,
::placeholder {
  font-family: "Space Grotesk", Courier, monospace, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  font-size: 100%;
  scroll-behavior: smooth;
  padding: 5px;
}

body {
  margin: 0;
  background: linear-gradient(89deg, #553d8d 0%, #006eff 100%);
}

button {
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 100px #006eff inset !important;
}

input:-webkit-autofill {
  -webkit-text-fill-color: #fff !important;
}

@media screen and (max-width: 1000px) {
  html {
    font-size: 85%;
  }
}

@media screen and (max-width: 750px) {
  html {
    font-size: 75%;
  }
}

@media screen and (max-width: 450px) {
  html {
    font-size: 65%;
  }
}

/* Scrollbar */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: linear-gradient(to bottom, #1f5cd5, #4f4196);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 3px;
}

